*{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

body{
  padding: 60px;
}

.card{
  align-content: center;
  border: 1px solid rgb(238, 137, 137);
  border-radius: 20px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}
.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}